import "./jobBoardStyles.css"
import React, {useEffect, useState} from 'react';
import YouTube from 'react-youtube';
import {useParams} from 'react-router-dom'; // Add this

function TestJobBoard() {
    const {id} = useParams(); // Get the id from the url


    const [videoEnded, setVideoEnded] = useState(false);
    const [player, setPlayer] = useState(null);
    const videoId = id; // Use id from URL // replace with your video ID


    const opts = {
        height: '480px',
        width: '854px',
        playerVars: {
            autoplay: 0,
            rel: 0,
            fs: 0// No related videos at the end
        },
    };

    const handleEnd = (event) => {
        // Video has ended
        setVideoEnded(true);
    };

    const onReady = (event) => {
        setPlayer(event.target);
    };

    useEffect(() => {
        let checkInterval = null;
        if (player) {
            checkInterval = setInterval(() => {
                const currentTime = player.getCurrentTime();
                const duration = player.getDuration();

                if (duration - currentTime <= 1) {
                    setVideoEnded(true);
                    clearInterval(checkInterval);
                }
            }, 1000); // checks every second
        }
        return () => {
            if (checkInterval) {
                clearInterval(checkInterval);
            }
        };
    }, [player]);

    const [emailAddress, setEmailAddress] = useState("")


    const handleSubmit = (e) => {
        //e.preventDefault()

        const encodedEmail = encodeURIComponent(emailAddress).replace(/%40/g, "@");

        window.parent.postMessage({action: "submittedVideoDemo", email: encodedEmail}, "*");
        //window.parent.location.href = "https://google.com"
    }

    return (
        <div style={{position: 'relative', height: '480px', width: '854px'}} className="video-container">
            <YouTube style={{height: '100%', width: '100%'}} videoId={videoId} opts={opts} onReady={onReady}/>
            {videoEnded && (
                <div className="cta-section">
                    <div className="cta-form-wrapper">
                        <img
                            src="https://assets.website-files.com/5f073e32d304276cc8b4ff30/5fbc3a22ab5229d67ae6a0cf_White%20Logo%20-%20Left%20Emblem.png"
                            className="top-logo" alt="doorloop_logo"></img>
                        <h2 className="demo-cta-header">Learn More About DoorLoop</h2>
                        <p>Schedule a demo in 36 seconds.</p>
                        <form className="video-cta-form" onSubmit={(e) => handleSubmit(e)}>
                            <div className="contact-field"><input type="email"
                                                                  onChange={(e) => setEmailAddress(e.target.value)}
                                                                  className="email-input-video-cta"
                                                                  autoComplete="on" autoFocus="true" maxLength="256"
                                                                  name="email" data-name="email"
                                                                  placeholder="name@company.com"
                                                                  id="video-cta-form-button"
                                                                  required=""/></div>
                            <div className="sign-btn-box"><input type="submit" value="Continue"
                                                                 data-wait="Please wait..."
                                                                 className="video-form-button"/>
                            </div>
                        </form>
                    </div>
                </div>
            )}
        </div>
        //<iframe src="https://santiaday.github.io/dl_calculators/test-job-board" width="640px" height="360px"></iframe>
    );
}

export default TestJobBoard;